<template>
    <views>
        <nav-bar
            title="中奖消息"
            fixed
            @click-left="onClickLeft">
        </nav-bar>
        <views class="gift">
            <van-list
                v-model="loadingData"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad">
                <views
                    v-for="(item, index) in list"
                    :key="index"
                    class="gift-list"
                    @click="handleDetail(item)">
                    恭喜您获得
                    <views class="gift-list-count">{{ item.gift_amount }}元</views>
                    {{ item.gift_name }}
                </views>
            </van-list>
        </views>
    </views>
</template>

<script>
import NavBar from '@/components/navBar'
import tools from '@/mixins/tools'
import api from '@/utils/api'
export default {
    components: {
        NavBar
    },
    mixins: [tools],
    data() {
        return {
            loadingData: false,
            finished: false,
            list: []
        }
    },
    methods: {
        onClickLeft() {
            this.back()
        },
        handleDetail(row) {
            this.push(`/gift-detail?id=${row.id}`)
        },
        onLoad() {
            const params = {
                member_id: '11111',
                page: 1,
                page_size: 10
            }
            api.getMemberPrizeList(params).then((res) => {
                if (res.list.length > 0) {
                    res.list.forEach((item) => {
                        this.list.push({ ...item })
                    })
                    this.loadingData = false
                } else {
                    this.finished = true
                }
            })
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
